
import bundleCssFile from "./code/bundle.css.enc.json"
import bundleJsFile from "./code/bundle.min.js.enc.json"
import testFile from "./code/test.txt.enc.json"

import CryptoJS from "crypto-js";

const bundleCss = bundleCssFile.file;
const bundleJs = bundleJsFile.file;
const test = testFile.file;

function hexToUtf8(s) {
    return decodeURIComponent(
        s.replace(/\s+/g, '') // remove spaces
            .replace(/[0-9a-f]{2}/g, '%$&') // add '%' before each 2 characters
    );
}

export class Locked {
    constructor() {
    }

    async unlock(code) {
        let d = this.decrypt(test, code);
        if (d !== 'valid') {
            alert('Неверный код доступа')
            throw new Error();
        }

        let css = this.decrypt(bundleCss, code);
        let js = this.decrypt(bundleJs, code);

        let customStyles = document.createElement('style');
        customStyles.appendChild(document.createTextNode( css ));
        document.body.insertAdjacentElement('afterbegin', customStyles);

        let customJs = document.createElement('script');
        customJs.appendChild(document.createTextNode( js ));
        document.body.insertAdjacentElement('afterbegin', customJs);
    }

    decrypt(encrypted, code) {
        return hexToUtf8(CryptoJS.AES.decrypt(encrypted, code).toString())
    }
}

const locked = new Locked();

export default locked;
