import {Form} from "react-bootstrap";
import {useCallback, useEffect, useRef, useState} from "react";
import "./LockInput.css";


const LockInput = ({className, onBackspace, onFilled, focus, value, setValue}) => {

    const onInput = useCallback((e) => {
        let v = e.target.value;
        if (v.length >= 4) {
            onFilled();
        }
        setValue(v);
    }, []);

    const onKeyDown = useCallback((e) => {
        let key = e.key;
        if (key !== "Backspace" && (!/\d/.test(key) || value.length >= 4)) {
            e.preventDefault();
        }
        if (key === "Backspace" && value.length === 0) {
            onBackspace();
        }
    }, [value]);

    const ref = useRef(null);

    useEffect(() => {
        if (focus) {
            ref.current.focus();
        }
    }, [focus]);

    return (
        <Form.Control
            ref={ref}
            className={`lock-input-control ${className}`}
            value={value}
            onInput={onInput}
            onKeyDown={onKeyDown}
        />
    );
};

export default LockInput;