import LockInput from "./LockInput";
import {useCallback, useRef, useState} from "react";

import "./Lock.css";

const Lock = ({values, setValues}) => {

    const setValue = (value, index) => {
        setValues(values => {
            values = [...values];
            values[index] = value
            return values
        })
    }

    const [focus, setFocus] = useState(0);

    const onFilled = useCallback((index) => {
        if (index !== 4) {
            setFocus(index + 1);
        }
    }, []);

    const onBackspace = useCallback((index) => {
        if (index !== 0) {
            setFocus(index - 1);
        }
    }, []);

    return (
        <div className="lock">
            <LockInput className="lock-input"
                       value={values[0]}
                       setValue={(v) => setValue(v, 0)}
                       onFilled={() => onFilled(0)}
                       onBackspace={() => onBackspace(0)}
                       focus={focus === 0}
            />
            <div className="lock-dash">
                &mdash;
            </div>
            <LockInput className="lock-input"
                       value={values[1]}
                       setValue={(v) => setValue(v, 1)}
                       onFilled={() => onFilled(1)}
                       onBackspace={() => onBackspace(1)}
                       focus={focus === 1}
            />
            <div className="lock-dash">
                &mdash;
            </div>
            <LockInput className="lock-input"
                       value={values[2]}
                       setValue={(v) => setValue(v, 2)}
                       onFilled={() => onFilled(2)}
                       onBackspace={() => onBackspace(2)}
                       focus={focus === 2}
            />
            <div className="lock-dash">
                &mdash;
            </div>
            <LockInput className="lock-input"
                       value={values[3]}
                       setValue={(v) => setValue(v, 3)}
                       onFilled={() => onFilled(3)}
                       onBackspace={() => onBackspace(3)}
                       focus={focus === 3}
            />
            <div className="lock-dash">
                &mdash;
            </div>
            <LockInput className="lock-input"
                       value={values[4]}
                       setValue={(v) => setValue(v, 4)}
                       onFilled={() => onFilled(4)}
                       onBackspace={() => onBackspace(4)}
                       focus={focus === 4}
            />
        </div>
    );
};

export default Lock;