
import "./Window.css";
import {Button, Card} from "react-bootstrap";
import Lock from "./Lock";
import {useCallback, useState} from "react";
import License from "./License";
import locked from "./locked";

const Window = (callback, deps) => {

    const [values, setValues] = useState([
        '',
        '',
        '',
        '',
        '',
    ]);

    const [showLicense, setShowLicense] = useState(false);

    const openLicense = useCallback(() => {
        setShowLicense(true);
    }, []);

    const unlock = useCallback(() => {
        locked.unlock(values.join(''))
    }, [values]);

    return (
        <>
            <License show={showLicense} setShow={setShowLicense}/>
            <Card className="window" body>
                <h1 className="window-item">
                    Транспортная задача
                </h1>
                <h2 className="window-item">
                    Оптимизация маршрутов снабжения
                </h2>
                <p className="window-item code">
                    Код доступа
                </p>
                <Lock className="window-item" values={values} setValues={setValues}/>
                <Button variant="outline-info" className="window-item start-button" onClick={unlock}>
                    Начать использование программы
                </Button>
                <p className="window-item">
                    Вводя код доступа и нажимая кнопку «Начать использование программы», вы соглашаетесь с условиями
                    {' '}
                    <a href="#" onClick={openLicense}>лицензии</a>.
                </p>
            </Card>
        </>
    );
};

export default Window;